import React from 'react';

import './styles.scss';

/**
 * Sign-in provider buttons view
 * 
 * @param {object} providers
 * @param {function} getLinkUrl
 */
const CompSignUpProviderButtons = ({
  providers,
  getLinkUrl,
}) => (
  <div>
    {providers.map((item) => (
      <div key={item.id}>
        <a href={getLinkUrl(item.id)} className="link">
          <button type="button" social={item.provider} style={{ width: '80%' }}>
            <i className={`fa fa-${item.provider}`} />
          </button>
        </a>
      </div>
    ))
    }
  </div>
);

export default CompSignUpProviderButtons;
