
import React from 'react';
import { withTranslation } from "react-i18next"
import { Link } from 'gatsby';

import { Layout } from "../components/Layout";
import SEO from "../components/SEO";

import { SignInForm } from '../components/SignIn';
import { SignUpProviderButtons } from '../components/SignUp';
import { PasswordForgetLink } from '../components/PasswordRecovery';
import { SIGN_UP, PASSWORD_FORGET } from '../constants/routes';

const I18N_ID = 'Others';

/**
 * Sign-in
 * 
 * @param {function} t i18n function
 */
const PageAuthSignin = ({
  t
}) => (
  <Layout>
    {/* page title */}
    <SEO title={t("auth.signin")} />

    <h1>{t("auth.signin")}</h1>

    <SignInForm t={t} />
    <SignUpProviderButtons />
    <br/>
    <p>
      <Link to={PASSWORD_FORGET}>{t('auth.forget-password')}</Link>
    </p>
    <p>
      <Link to={SIGN_UP}>{t('auth.no-account')}</Link>
    </p>
  </Layout>
);

export default withTranslation(I18N_ID)(PageAuthSignin);
