//import React, { useState, useEffect } from 'react';
import React, { Component, ReactElement } from 'react';
import { Link } from 'gatsby';

import { ComponentProps } from '../ComponentProps.d';
import CompPasswordRecoveryRequestForm, { CompPasswordRecoveryRequestSent } 
  from '../../views/comp-password-recovery/comp-password-recovery-request-form';

import CompPasswordRecoveryResetForm, { CompPasswordRecoveryResetSent }
  from '../../views/comp-password-recovery/comp-password-recovery-reset-form';

import { forgetPassword, resetPassword } from '../../services/auth';

import * as ROUTES from '../../constants/routes';

const _log = require('debug')('app:comp:PasswordRecovery');

//
// Forget-Password Form
//
const FORGET_PASSWORD_INIT_STATE = {
  email: '',
  error: '',
  sent: false,
}

interface ForgetPasswordFormProps extends ComponentProps {
  resetPasswordUrl: string;
}
type ForgetPasswordFormStates = typeof FORGET_PASSWORD_INIT_STATE;

export class ForgetPasswordForm extends Component<ForgetPasswordFormProps, ForgetPasswordFormStates> {
  state = FORGET_PASSWORD_INIT_STATE;

  onUpdateField = (e: React.ChangeEvent): void => {
    //@ts-ignore
    this.setState({[e.target.name]: e.target.value});
  }
  
  onSubmit = async (e: React.FormEvent): Promise<void> => {
    e.preventDefault();

    _log(this.state.email, this.props.resetPasswordUrl);
    const res = await forgetPassword(this.state.email, this.props.resetPasswordUrl)
    if (res.kind === 'ok') {
      this.setState({ error: '', sent: true });
    }
    else {
      this.setState({ ...FORGET_PASSWORD_INIT_STATE, error: res.kind });
    }
  }

  render(): React.ReactElement {
    const { email, error } = this.state;
    const isSubmitButtonDisabled = !_isValidEmail(email);

    if (this.state.sent) {
      return (
        <CompPasswordRecoveryRequestSent email={email} {...this.props} />
      );
    }
    else {
      return (
        <CompPasswordRecoveryRequestForm 
          t={this.props.t}
          onSubmit={this.onSubmit}
          email={email}
          onChange={this.onUpdateField}
          isSubmitButtonDisabled={isSubmitButtonDisabled}
          errorMessage={error}
        />
      );
    }
  }
}

//
// Reset-Password Form
//
const RESET_PASSWORD_INIT_STATE = {
  password: '',
  passwordConfirmation: '',
  error: '',
  sent: false,
};

interface ResetPasswordFormProps extends ComponentProps {
  resetPasswordCode: string;
}
type ResetPasswordFormStates = typeof RESET_PASSWORD_INIT_STATE;


export class ResetPasswordForm extends Component<ResetPasswordFormProps, ResetPasswordFormStates> {
  state = RESET_PASSWORD_INIT_STATE;

  onUpdateField = (e: React.ChangeEvent): void => {
    //@ts-ignore
    this.setState({[e.target.name]: e.target.value});
  }
  
  onSubmit = async (e: React.FormEvent): Promise<void> => {
    e.preventDefault();

    const res = await resetPassword(this.props.resetPasswordCode, this.state.password, this.state.passwordConfirmation)
    if (res.kind === 'ok') {
      this.setState({ ...RESET_PASSWORD_INIT_STATE, sent: true });
    }
    else {
      this.setState({ ...RESET_PASSWORD_INIT_STATE, error: res.kind });
    }
  }

  componentDidUpdate(): void {
    // after show reset password result, then go to sign-in page
    // if (this.state.sent) navigate(ROUTES.SIGN_IN);
  }

  render(): React.ReactElement {
    const { password, passwordConfirmation, error } = this.state;
    const isSubmitButtonDisabled = !_isValidPassword(this.state);

    if (this.state.sent) {
      return (
        <CompPasswordRecoveryResetSent {...this.props} />
      );
    }
    else {
      return (
        <CompPasswordRecoveryResetForm
          onSubmit={this.onSubmit}
          password={password}
          passwordConfirmation={passwordConfirmation}
          onChange={this.onUpdateField}
          isSubmitButtonDisabled={isSubmitButtonDisabled}
          errorMessage={error}
          t={this.props.t}
        />
      );
    }
  }
}

export const PasswordForgetLink = (): ReactElement => (
  <p>
    <Link to={ROUTES.PASSWORD_FORGET}>Forgot Password?</Link>
  </p>
);

const _isValidEmail = (value: string): boolean => (
  value !== ''
);

const _isValidPassword = (form: any): boolean => (
  form.password !== '' &&
  form.passwordConfirmation !== '' &&
  form.password === form.passwordConfirmation
);
