import React, { Component } from 'react';
import { navigate } from 'gatsby';

import { signup } from '../../services/auth';
import * as ROUTES from '../../constants/routes';
import providers, {getLinkUrl} from '../../constants/providers';

import { ComponentProps } from '../ComponentProps.d';

import CompSignUpForm from '../../views/comp-sign-up/comp-sign-up-form';
import CompSignUpProviderButtonsView from '../../views/comp-sign-up/comp-sign-up-provider-buttons';

const INITIAL_STATE = {
  username: '',
  email: '',
  password: '',
  passwordConfirmation: '',
  error: '',
};

interface SignUpFormProps extends ComponentProps {
}

type SignUpFormStates = typeof INITIAL_STATE;

class SignUpForm extends Component<SignUpFormProps, SignUpFormStates> {

  state = INITIAL_STATE;

  onSubmit = (event: React.FormEvent) => {
    const { username, email, password } = this.state;

    signup({username, email, password})
      .then((res) => {
        if (!res || res.kind !== 'ok') {
          this.setState({...INITIAL_STATE, error: res ? res.kind : 'unknown-error'});
        }
        else {
          this.setState({ ...INITIAL_STATE });
          navigate(ROUTES.USER_HOME);
        }
      })
      .catch(error => {
        this.setState({ error: error.message || error.toString() });
      });

    event.preventDefault();
  };

  onChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    // !!! The variable, 'name' shold be 'username', 'email' or 'password'
    //@ts-ignore
    const { name, value } = event.target;
    //@ts-ignore
    this.setState({ [name]: value });
  };

  isValidFormData(): boolean {
    const { username, email, password, passwordConfirmation } = this.state;
    // simple check
    return !!(username && password && email && (password === passwordConfirmation));
  }

  render() {
    const isSubmitButtonDisabled = !this.isValidFormData();
    const { username, email, password, passwordConfirmation, error } = this.state;

    // TODO: fix type error
    // @ts-ignore
    return ( <CompSignUpForm
      onSubmit={this.onSubmit}
      username={username}
      onChangeUsername={this.onChange}
      email={email}
      onChangeEmail={this.onChange}
      password={password}
      onChangePassword={this.onChange}
      passwordConfirmation={passwordConfirmation}
      onChangePasswordConfirmation={this.onChange}
      isSubmitButtonDisabled={isSubmitButtonDisabled}
      errorMessage={error}
      {...this.props}
      />
    );
  }
}

const SignUpProviderButtons = () => (
  <CompSignUpProviderButtonsView
  providers={providers.filter((item) => item.id !== 'email')}
  getLinkUrl={getLinkUrl}
  />
);

export { SignUpForm, SignUpProviderButtons };
