import React from 'react';

import './styles.scss';

/**
 * Sign-in form view
 * 
 * @param {function} t i18n function
 */
const CompSignInForm = ({
  t,
  onSubmit,
  email,
  onChangeEmail,
  password,
  onChangePassword,
  isSubmitButtonDisabled,
  errorMessage
}) => (
  <form onSubmit={onSubmit}>
    <input
      name="email"
      value={email}
      onChange={onChangeEmail}
      type="text"
      placeholder="Email Address"
      autoComplete="username"
    />
    <input
      name="password"
      value={password}
      onChange={onChangePassword}
      type="password"
      placeholder="Password"
      autoComplete="current-password"
    />
    <button disabled={isSubmitButtonDisabled} type="submit">
      {t('auth.form-sign-in-button')}
    </button>

    {errorMessage && <p>{errorMessage}</p>}
  </form>
);

export default CompSignInForm;
