import {API_BASE_URL} from '../config';

const providers = [
//  'discord', 
//  'facebook', 
//  'github', 
{id: 'email', provider: 'email', enabled: true},
{id: 'google.com', provider: 'google', enabled: true}
//  'microsoft', 
//  'twitch', 
//  'twitter'
];

export function getLinkUrl(providerName: string): string {
  return `${API_BASE_URL}/connect/${providerName}`;
}

export function isAvailable(providerId: string): boolean {
  for (let i = 0; i < providers.length; i ++) {
    if (providerId === providers[i].id) return providers[i].enabled;
  }
  return false;
}

export default providers;
