import React from 'react';
import { Link } from 'gatsby';

import { SIGN_IN } from '../../constants/routes';

/**
 * ResetPassword form view
 * 
 * @param {TFunction} t i18n function
 * @param {(e: FormEvent<Element>) => Promise<void>} onSubmit
 * @param {string} password
 * @param {string} passwordConfirmation
 * @param {(e: ChangeEvent<Element>) => void} onChange
 * @param {boolean} isSubmitButtonDisabled
 * @param {string} errorMessage
 */
const CompPasswordRecoveryResetForm = ({
  t,
  onSubmit,
  password,
  passwordConfirmation,
  onChange,
  isSubmitButtonDisabled,
  errorMessage
}) => (
  <>
  <h4>{t("auth.pwrecovery-reset")}</h4>

  <form onSubmit={onSubmit}>
    <input
      name="password"
      value={password}
      onChange={onChange}
      type="password"
      placeholder="Password"
    /> <br/>
    <input
      name="passwordConfirmation"
      value={passwordConfirmation}
      onChange={onChange}
      type="password"
      placeholder="Password Confirmation"
    /> <br/>

    <button disabled={isSubmitButtonDisabled} type="submit">
      {t('auth.form-reset-password-button')}
    </button> <br/>

    {errorMessage && <p>{errorMessage}</p>}
  </form>
  </>
);

export const CompPasswordRecoveryResetSent = ({
  t,
}) => (
  <>
    <h4>{t('auth.reset-password-form-sent')}</h4>
    <Link to={SIGN_IN}>
      <button>
        {t('auth.reset-password-form-sent-button')}
      </button>
    </Link>
  </>
);

export default CompPasswordRecoveryResetForm
