import React from 'react';

/**
 * ForgetPassword form view
 * 
 * @param {TFunction} t i18n function
 * @param {(e: FormEvent<Element>) => Promise<void>} onSubmit
 * @param {string} email
 * @param {(e: ChangeEvent<Element>) => void} onChange
 * @param {boolean} isSubmitButtonDisabled
 * @param {string} errorMessage
 */
const CompPasswordRecoveryRequestForm = ({
  t,
  onSubmit,
  email,
  onChange,
  isSubmitButtonDisabled,
  errorMessage,
}) => (
  <>
  <h4>{t("auth.pwrecovery-forget")}</h4>

  <form onSubmit={onSubmit}>
    <input
      name="email"
      value={email}
      onChange={onChange}
      type="text"
      placeholder="Email"
    /> <br/>
    <button disabled={isSubmitButtonDisabled} type="submit">
      {t('auth.form-forget-password-button')}
    </button> <br/>

    {errorMessage && <p>{errorMessage}</p>}
  </form>
  </>
);

export const CompPasswordRecoveryRequestSent = ({
  t,
  email,
}) => (
  <>
  <h4>{t('auth.forget-password-form-sent', {email})}</h4>
  </>
);

export default CompPasswordRecoveryRequestForm;

